.App {
  font-family: sans-serif;
  text-align: center;
}

.content-container {
  padding-bottom: 60px; /* adjust this value to match the height of your fixed nav element */
}

.nav {
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  display: flex;
  overflow-x: auto;
  background-color: #ffffff;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  min-width: 50px;
  color: #444444;
}

.nav__link:hover {
  background-color: #eeeeee;
}

.nav__link.active {
  color: #009578;
}
